/* Style the input container */
.c-container-input {
    display: flex;
    width: 100%;
    margin-bottom: 8px;
}

/* Style the form icons */
.c-icon {
    padding: 16px;
    background: dodgerblue;
    color: white;
}

/* Style the input fields */
.c-input-field {
    width: 100%;
    padding: 10px;
    outline: none;
    &:focus {
        border: 2px solid dodgerblue;
    }
}

/* Style the input fields */
.c-btn-register {
    padding: 16px 20px;
    border: none;
    color: white;
    width: 100%;
    background-color: dodgerblue;
}
